@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

* {
  background-color: #1a1a1c;
  font-family: "Montserrat", sans-serif;
  color: #fff;
}

.main-cointainer {
  display: flex;
  text-align: center;
  width: 75%;
}

.name,
.curr_name,
.short-form,
.current-price,
.market-cap,
.percentage-change,
.volume {
  text-align: center;
}

.curr_name {
  display: flex;
  justify-content: start;
  margin-left: 20px;
  width: 220px;
}

.main-cointainer {
  font-size: 16px;
  margin-bottom: 30px;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

.name {
  width: 200px;
  margin-left: 28px;
}

.heading {
  margin-bottom: 32px;
  text-align: center;
}
form{
    text-align: center;
}

.coin-input {
  margin-bottom: 50px;
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

.short-form {
  width: 100px;
  padding: 0px;
}

.current-price {
  width: 150px;
}

.market-cap {
  width: 250px;
}

.percentage-change {
  width: 140px;
}

.name {
  display: flex;
}
.volume {
  width: 300px;
}

.image_crypto {
  width: 32px;
  height: 32px;
  margin-bottom: 5px;
}


.main-cointainer {
  margin-left: 200px;
  margin-right: 200px;
  margin-top: 30px;
  border-bottom: 1px solid #d7d7d7;
}
